.footer-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-height: 30vh;
  margin-top: -80px;
}

.container.footer-container h1 {
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: 100;
  font-size: 25px;
  margin: 20px auto;
  text-align: center;
  text-transform: capitalize;
}

.container.footer-container h2 {
  font-family: "Open Sans Condensed";
  font-weight: 100;
  font-size: 20px;
  margin-top: 15px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 10px 20px;
  border-radius: 5px;
}
.container.footer-container h2:hover {
  background-color: rgba(0, 0, 0, 0.068);
}

.social-links {
  padding: 10px;
  margin: 20px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.social-links .social {
  height: 45px;
  width: 45px;
  background-color: rgba(0, 0, 0, 0.548);
  margin: 0 5px;
  padding: 10px;
  backdrop-filter: blur(10px);
  cursor: pointer;
  transition: background 0.2s ease;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

.social-links .social:hover {
  background-color: rgba(255, 255, 255, 0.336);
  fill: var(--color-pri);
}
@media (max-width: 376px) {
  .footer-container {
    margin-top: -300px;
  }
}
@media (max-width: 420px) {
  .footer-container {
    margin-top: -270px;
  }
}
@media (max-width: 500px) {
  .footer-container {
    margin-top: -270px;
  }
}
@media (max-width: 820px) {
  .footer-container {
    margin-top: -220px;
    margin-bottom: 50px;
  }
}
